import { useState } from "react";
import store from "../../store";
import { saveChecklistGoal } from "../../containers/cbChecklist/checkListMethods";
import DisplayOrderChooser from "./DisplayOrderChooser";

const GoalForm = () => {
    let currentChecklist = store.getState().siteModelChecklist.currentChecklist ? store.getState().siteModelChecklist.currentChecklist : {};

    let startGoal = store.getState().siteModelChecklist?.editGoal ? store.getState().siteModelChecklist.editGoal : {checklistuuid:currentChecklist.id};


    let [goal,setGoal] = useState(startGoal);
    let goals = store.getState().siteModelChecklist?.currentChecklistGoals ? store.getState().siteModelChecklist.currentChecklistGoals : [];
    let saveButton = goal?.isDirty ? <button className={"btn btn-sm btn-secondary"} onClick={()=>{setGoal(Object.assign({},goal,{"isDirty":false})); saveChecklistGoal(goal)}}>Save</button> : null;

    return <div>
        <div>Name: <input value={goal.label} type={"text"} onChange={(evt)=>setGoal(Object.assign({},goal,{"label":evt.currentTarget.value,isDirty:true}))} /></div>
        <div>Active: <input type={"checkbox"} checked={goal.active} onChange={(evt)=>setGoal(Object.assign({},goal,{"active":evt.currentTarget.checked ? 1 : 0,isDirty:true}))} /> </div>
        <div>Display Order: <input type="text" value={goal.displayOrder} onChange={(evt)=>setGoal(Object.assign({},goal,{"displayOrder":evt.currentTarget.value,isDirty:true}))} /></div>
        <div>{saveButton} </div>
    </div>
}
//<DisplayOrderChooser length={goals.length} action={(evt)=>{}} />
export default GoalForm;